.Icon-container {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-container-check {
  position: absolute;
  left: 12px;
}
.Icon-container-edit {
  position: absolute;
  top: -24px;
  right: 48px;
}
.Icon-container-delete {
  position: absolute;
  top: -24px;
  right: 0;
}
.Icon-svg {
  width: 24px;
  height: 24px;
}

/* Hover */
.Icon-container-delete:hover .Icon-svg {
  fill: red;
}
.Icon-container-check:hover .Icon-svg {
  fill: #4caf50;
}
.Icon-container-edit:hover .Icon-svg {
  fill: #61DAFA;
}